<template>
  <el-select
    filterable
    v-model="Value"
    :placeholder="placeholder"
    @change="ChangeHandle"
    clearable
    collapse-tags
    :multiple="multiple"
    :size="size"
    reserve-keyword
  >
    <el-option
      v-for="item in Datas"
      :key="item.MetadataCode"
      :label="item.MetadataName"
      :value="item.MetadataCode"
    ></el-option>
  </el-select>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    size: {
      type: String,
      default: "mini"
    },
    /**
     * 
     */
    multiple: {
      type: Boolean,
      default: false
    },
    value: {},
    placeholder: {
      type: String,
      default: ""
    },
    code: {
      type: String,
      default: ""
    }
  },
  watch: {
    code(newVal, oldVal) {
      if (newVal !== oldVal) this.LoadData();
    },
    value(newVal, oldVal) {
      this.Value = this.value;
      if (newVal !== oldVal) this.LoadData();
    }
  },
  data() {
    return {
      Value: this.value,
      Datas: []
    };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    ChangeHandle() {
      this.$emit("update:value", this.Value);
      const s = this.Datas.find(c => c.MetadataCode === this.Value);
      if (s) {
        this.$emit("change", s.MetadataName);
      }
      if (this.Value && this.multiple) {
        let val = "";
        this.Value.forEach((f, i) => {
          val += f;
          if (i < this.Value.length - 1) {
            val += ",";
          }
        });

        this.$emit("Filter", val);
      } else {
        this.$emit("Filter", this.Value);
      }
    },
    async LoadData() {
      const res = await api.Metadata.GetTrees(this.code);
      if (res) {
        this.Datas = [];
        res.Datas.forEach(element => {
          if (element.MetadataCode !== this.code) {
            this.Datas.push(element);
          }
        });
        this.Value = this.value;
      }
    }
  }
};
</script>

<style lang="less"></style>
