<template>
  <div class="record_module">
    <div class="module_header">
      <div class="name"></div>
      <div>
        <el-select v-model="groupTime" size="mini" @change="getGroupRanking" :style="{textAlign: 'center'}">
          <el-option v-for="(item, index) in groupList" :key="index" :label="item.text" :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>

    <div v-loading="listLoading">
      <el-row :gutter="20" v-if="groupRanking && groupRanking.length>0">
        <el-col :span="4" v-for="(item, index) in groupRanking" :key="index" :style="{padding:'10px'}">
          <el-card shadow="hover" class="list_card blue_bg">
            <el-row :style="{lineHeight: '35px'}">
              <el-col :span="20">{{item['GroupName']}}</el-col>
              <el-col :span="4">
                <el-tag v-if="item['Ranking']===1" size="small" type="danger">{{item['Ranking']}}</el-tag>
                <el-tag v-else-if="item['Ranking']===2" size="small" type="success">{{item['Ranking']}}
                </el-tag>
                <el-tag v-else-if="item['Ranking']===3" size="small">{{item['Ranking']}}</el-tag>
                <el-tag v-else type="info" size="small">{{item['Ranking']}}</el-tag>
              </el-col>
            </el-row>
            <div class="card_info">
              <div class="card_line">
                <div>{{item['TotalScore']}}</div>
                <div>{{item['TotalGood']}}</div>
                <el-button size="mini" type="success" @click="viewMember(item)" style="padding: 7px 15px;">
                </el-button>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <div v-else></div>
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" :close-on-click-modal="false" :modal-append-to-body="false">
      <el-table :data="memberList" max-height="550" stripe
                :default-sort="{prop: 'signTime', order: 'descending'}">
        <!--        <el-table-column type="index" width="70" label="">-->
        <!--        </el-table-column>-->
        <el-table-column property="Ranking" label="" sortable></el-table-column>
        <el-table-column property="StudentName" label=""></el-table-column>
        <el-table-column property="TotalGood" label="" sortable></el-table-column>
        <el-table-column property="TotalScore" label="" sortable></el-table-column>

        <el-table-column property="IsGroupLeader" label="">
          <template slot-scope="scope">
            <el-tag :type="scope.row['IsGroupLeader'] ? 'success' : 'primary' " disable-transitions>
              {{ scope.row['IsGroupLeader'] ? '' : ''}}
            </el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { getSession } from "@/scripts/utils";

export default {
  name: "GroupScore",
  props: {
    groupList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),
      groupTime: null,
      groupRanking: [],

      listLoading: false,
      memberList: [],
      dialogVisible: false
    };
  },
  methods: {
    initSelect () {
      this.$nextTick(() => {
        if (this.groupList && this.groupList.length > 0) {
          this.groupTime = this.groupList[0].value;
          this.getGroupRanking();
        }
      });
    },
    // 
    getGroupRanking () {
      this.groupRanking = [];
      api.IGroup.DataRank(this.courseRecordID, this.groupTime).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.groupRanking.push(item);
          });
        }
      });
    },
    viewMember (item) {
      this.dialogVisible = true;
      this.listLoading = true;
      this.memberList = [];
      api.IGroup.DataMemberRank(item["GroupID"]).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.memberList.push(item);
          });
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      });
    }
  },
  watch: {
    groupList (newVal, oldVal) {
      if (newVal && newVal.length > 0) this.initSelect();
    }
  }
};
</script>

<style lang="less" scoped>
@import "./../../style/record";

.list_card {
  .card_info {
    color: #2c3e50;
    line-height: 2.5;

    .card_line {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
    }
  }
}

</style>
