<template>
  <div id="comTable" ref="div_Table" class="tbl" :class="{'single-tbl':singleChoose}">
    <el-button
      type="primary"
      size="mini"
      @click="CreateHandle"
      v-if="Subbtn && ShowCreate"
    >{{ ModuleName }}</el-button>
    <el-table
      v-loading="Loading"
      ref="Table"
      :data="Datas"
      :fit="true"
      class="tblData"
      style="width: 100%"
      :row-style="rowStyle"
      @row-click="rowClick"
      :max-height="MaxTblHeight"
      @selection-change="SelectionChanged"
      @filter-change="FilterChanged"
      :row-key="KeyFileld"
    >
      <el-table-column
        class-name="sel_col"
        type="selection"
        width="35"
        v-if="ShowSelectColumn"
        fixed="left"
      ></el-table-column>
      <el-table-column
        :class-name="column.Code"
        :align="column.Align"
        :prop="column.Code"
        :column-key="column.Code"
        :label="column.Header"
        :width="column.Width"
        :min-width="column.MinWidth"
        :fixed="column.Fixed"
        :filter-multiple="column.FilterMultiple === true"
        :filters="column.Filters"
        :filtered-value="column.FilteredValue"
        v-for="(column, index) in Columns"
        :show-overflow-tooltip="column.Code !== 'action_column'"
        :key="index"
      >
        <template slot="header" v-if="column.HeaderSlot">
          <slot :name="`${column.Code}Header`" />
        </template>
        <template slot-scope="scope">
          <span v-if="!column.Slot && !column.IsEstate">{{ scope.row[column.Code] }}</span>
          <i
            v-if="column.IsEstate"
            class="estate"
            :class="{
              'el-icon-circle-check': parseInt(scope.row[column.Code]) === 1,
              'el-icon-remove': parseInt(scope.row[column.Code]) === 0
            }"
          >{{ parseInt(scope.row[column.Code]) === 1 ? "" : "" }}</i>
          <slot
            v-if="column.Slot && !column.IsEstate"
            :name="column.Code"
            :row="scope.row"
            :index="index"
            :value="scope.row[column.Code]"
          />
          <el-button-group v-if="column.Slot && column.ShowBaseActionBtn">
            <el-button
              v-if="column.NeedViewBtn"
              onfocus="this.blur()"
              type="primary"
              icon="el-icon-view"
              size="mini"
              plain
              @click="ViewHandle(scope.row)"
            ></el-button>
            <el-button
              v-if="!column.UnNeedEditBtn"
              :disabled="scope.row['EditDisabled']"
              onfocus="this.blur()"
              type="primary"
              icon="el-icon-edit"
              size="mini"
              plain
              @click="EditHandle(scope.row)"
            ></el-button>
            <slot name="ExAction" :row="scope.row" />
            <el-button
              v-if="!column.UnNeedEstateBtn && scope.row[EstateFiled] == 1"
              onfocus="this.blur()"
              plain
              size="mini"
              type="warning"
              icon="el-icon-remove-outline"
              @click="UpdateEstate(scope.row, 0)"
            ></el-button>
            <el-button
              onfocus="this.blur()"
              v-if="!column.UnNeedEstateBtn && scope.row[EstateFiled] != 1"
              plain
              type="success"
              icon="el-icon-circle-check"
              size="mini"
              @click="UpdateEstate(scope.row, 1)"
            ></el-button>
            <el-button
              onfocus="this.blur()"
              size="mini"
              v-if="scope.row[EstateFiled] != -1"
              plain
              type="danger"
              icon="el-icon-close"
              @click="Delete(scope.row)"
            ></el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <div class="el-table el-table--fit" ref="TblFoot" v-if="Subbtn">
      <div class="el-table__footer-wrapper">
        <table cellspacing="0" cellpadding="0" border="0" class="el-table__footer">
          <colgroup>
            <col width="40" v-if="ShowSelectColumn && !singleChoose" />
            <col width="100%" />
          </colgroup>
          <tfoot>
            <tr class>
              <td v-if="ShowSelectColumn && !singleChoose">
                <div class="cell">
                  <el-checkbox
                    v-model="CheckAll"
                    :indeterminate="isIndeterminate"
                    @change="CheckALLChanged"
                  ></el-checkbox>
                </div>
              </td>
              <td>
                <div class="cell">
                  <el-button-group v-if="ShowSelectColumn && !singleChoose">
                    <el-button
                      icon="el-icon-remove-outline"
                      size="mini"
                      :disabled="!isIndeterminate && !CheckAll"
                      @click="UpdateEstate(null, 0)"
                    ></el-button>
                    <el-button
                      icon="el-icon-circle-check"
                      size="mini"
                      :disabled="!isIndeterminate && !CheckAll"
                      @click="UpdateEstate(null, 1)"
                    ></el-button>
                    <el-button
                      icon="el-icon-close"
                      size="mini"
                      :disabled="!isIndeterminate && !CheckAll"
                      @click="Delete()"
                    ></el-button>
                    <slot name="BatchAction" />
                  </el-button-group>
                  <el-pagination
                    v-loading="Loading"
                    v-if="ShowPage"
                    :current-page.sync="PIndex"
                    :page-size.sync="PSize"
                    :total="TotalCounts"
                    @size-change="PageSizeChanged"
                    @current-change="PageChanged"
                    layout="total, sizes, prev, pager, next, jumper"
                  ></el-pagination>
                </div>
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
    <el-dialog
      :custom-class="'dlgForm' + ' ' + Mclass"
      :title="`${IsEdit ? '' : ''}${ModuleName}`"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      :modal-append-to-body="false"
      append-to-body
    >
      <el-form
        label-position="right"
        :model="BaseForm"
        :rules="BaseFormRule"
        ref="BaseForm"
        size="mini"
        status-icon
        @keyup.enter.native="ConfirmBaseForm"
        :label-width="LabelW + 'px'"
      >
        <slot name="BaseForm" />
      </el-form>
      <div slot="footer">
        <el-button-group>
          <el-button
            type="primary"
            icon="el-icon-check"
            size="mini"
            @click="ConfirmBaseForm"
            autofocus
            native-type="submit"
          > </el-button>
          <el-button icon="el-icon-close" size="mini" @click="dialogVisible = false"> </el-button>
        </el-button-group>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    // 
    KeyFileld: {
      type: String,
      default: "",
    },
    // 
    EstateFiled: {
      type: String,
      default: "",
    },
    // 
    Loading: {
      type: Boolean,
      default: false,
    },
    Datas: {
      type: Array,
      default: () => [],
    },
    // 
    ShowSelectColumn: {
      type: Boolean,
      default: true,
    },
    // 
    Columns: {
      type: Array,
      default: () => [],
    },
    LabelW: {
      type: Number,
      default: 120,
    },
    // 
    BaseForm: {
      type: Object,
      default: null,
    },
    // 
    BaseFormRule: {
      type: Object,
      default: null,
    },
    // 
    ModuleName: {
      type: String,
      default: "",
    },
    // class
    Mclass: {
      type: String,
      default: "",
    },
    // 
    ShowCreate: {
      type: Boolean,
      default: true,
    },
    Subbutton: {
      type: Boolean,
      default: true,
    },
    // 
    ShowPage: {
      type: Boolean,
      default: false,
    },
    // 
    PageIndex: {
      type: Number,
      default: 1,
    },
    // 
    PageSize: {
      type: Number,
      default: 20,
    },
    // 
    TotalCounts: {
      type: Number,
      default: 0,
    },
    // 
    singleChoose: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    $route: function (newRouter, oldRouter) {
      this.$emit("update:PageIndex", 1);
      this.$emit("LoadData");
    },
    PageIndex(newVal, oldVal) {
      this.PIndex = newVal;
    },
    Subbutton(val) {
      this.Subbtn = val;
    },
  },
  data() {
    return {
      Subbtn: this.Subbutton,
      CheckAll: false,
      isIndeterminate: false,
      MaxTblHeight: null,
      Selections: [],
      dialogVisible: false,
      IsEdit: false,
      PIndex: this.PageIndex,
      PSize: this.PageSize,
    };
  },
  mounted() {
    this.MaxTblHeight = parseInt(window.screen.height) * 0.4;
  },
  methods: {
    /**
     * 
     */
    SelectionChanged(val) {
      if (val && val.length > 0) {
        if (val.length === this.Datas.length) {
          this.CheckAll = true;
          this.isIndeterminate = false;
        } else {
          this.CheckAll = false;
          this.isIndeterminate = true;
        }
      } else {
        this.CheckAll = false;
        this.isIndeterminate = false;
      }

      this.$emit("update:selections", val);
      this.Selections = val;
    },
    /**
     * 
     */
    CheckALLChanged() {
      this.$refs.Table.toggleAllSelection();
    },
    /**
     *  key  column  columnKey value 
     */
    async FilterChanged(filters) {
      try {
        const key = Object.keys(filters)[0];
        // 
        const column = this.Columns.find((c) => c.Code === key);

        let value = filters[key][0];

        if (column && column.FilterMultiple) {
          value = "";
          await filters[key].forEach((f, i) => {
            value += f;
            if (i < filters[key].length - 1) {
              value += ",";
            }
          });
        }

        this.$emit("update:PageIndex", 1);
        this.$emit(`update:${key}`, value);
        this.$emit("LoadData");
      } catch {}
    },
    /**
     * 
     */
    PageSizeChanged(val) {
      this.$emit("update:PageIndex", 1);
      this.$emit("update:PageSize", val);
      this.$emit("LoadData");
    },
    /**
     * 
     * @param {number} val 
     */
    PageChanged(val) {
      this.$emit("update:PageIndex", val);
      this.$emit("update:PageSize", this.PageSize);
      this.$emit("LoadData");
    },
    /**
     * 
     */
    async UpdateEstate(row, estate) {
      if (row) {
        this.$emit("UpdateEstate", row[this.KeyFileld], estate);
      } else {
        if (this.Selections.length > 0) {
          let ids = "";
          await this.Selections.forEach((row) => {
            ids += row[this.KeyFileld] + ",";
          });

          this.$emit("UpdateEstate", ids, estate);
        }
      }
    },
    /**
     * 
     */
    Delete(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          if (row) {
            this.$emit("Delete", row[this.KeyFileld]);
          } else {
            if (this.Selections.length > 0) {
              let ids = "";
              await this.Selections.forEach((row) => {
                ids += row[this.KeyFileld] + ",";
              });

              this.$emit("Delete", ids);
            }
          }
        })
        .catch(() => {});
    },
    /**
     * 
     */
    EditHandle(row) {
      this.IsEdit = true;
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs["BaseForm"].resetFields();
        this.$emit("EditRowHandle", row);
      });
    },
    /**
     * 
     */
    ViewHandle(row) {
      this.$emit("ViewRowHandle", row);
    },
    ConfirmBaseForm() {
      this.$refs.BaseForm.validate(async (valid) => {
        if (valid) {
          await this.$emit("ConfirmBaseForm", () => {
            this.dialogVisible = false;
            this.$emit("LoadData");
          });
        }
      });
    },
    /**
     * 
     */
    CreateHandle() {
      this.IsEdit = false;
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs["BaseForm"].resetFields();
        this.$emit("CreateHandle");
      });
    },
    rowStyle({ row, rowIndex }) {
      if (this.singleChoose) {
        Object.defineProperty(row, "rowIndex", {
          // rowIndex
          value: rowIndex,
          writable: true,
          enumerable: false,
        });
      }
    },
    /**
     * 
     */
    rowClick(row, column, event) {
      if (this.singleChoose) {
        const refsElTable = this.$refs.Table; // 
        const findRow = this.Selections.find(
          (c) => c.rowIndex === row.rowIndex
        );
        if (findRow) {
          refsElTable.toggleRowSelection(row, false);
          return;
        }
        refsElTable.clearSelection();
        refsElTable.toggleRowSelection(row); // 
      }
    },
  },
};
</script>

<style lang="less">
#comTable {
  margin-top: -10px;
  &.single-tbl {
    .el-table__header thead tr th {
      .el-checkbox {
        display: none;
      }
    }
    .el-table__body tbody tr td:first-child {
      position: relative;
    }
    .el-table__body tbody tr td:first-child::before {
      content: "";
      position: absolute;
      width: 25px;
      height: 24px;
      top: 10;
      left: 10;
      z-index: 55;
    }
  }
  .el-pagination {
    float: right;
  }
}
.dlgForm {
  .el-date-editor.el-input,
  .el-date-editor.el-input__inner {
    width: 100%;
  }
  .el-input-number--mini {
    width: 100%;
  }
  .el-dialog__header {
    text-align: center;
    border-bottom: 1px solid #ddd;
  }
}
.el-table__footer {
  width: 100%;
}
</style>
