<template>
  <div class="stratergy">
    <div class="strategyList">
      <div
        class="stritem"
        :class="{'Enable' : item.state === 1 , 'selection' : selIndex === index }"
        v-for="(item,index) in  stras"
        :key="index"
        @click="selection(item,index)"
      >
        <i class="iconfont icon_plan"></i>
        <span>{{item.name}}</span>
      </div>
      <div class="increase" @click="increase()">
        <i class="iconfont icon_plus"></i>
        <span></span>
      </div>
    </div>
    <p></p>
    <div class="strategyForm">
      <el-form ref="form" :model="form" :rules="rules" size="mini" label-width="90px">
        <el-row :gutter="5">
          <el-col :span="12">
            <el-form-item label=":" prop="name">
              <el-input v-model="form.name" minlength="2" maxlength="20" clearable show-word-limit></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=":" prop="mode">
              <el-select v-model="form.mode" @change="clearValidate" placeholder="" filterable>
                <el-option
                  v-for="(item,index)  in modeOpts"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=":" prop="type">
              <el-select v-model="form.type" @change="clearValidate" placeholder="" filterable>
                <el-option
                  v-for="(item,index)  in typeOpts"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="12" v-if="form.mode != 0">
            <el-form-item label=":" prop="startDate">
              <el-date-picker class="date-start" type="date" v-model="form.startDate" clearable></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode != 0">
            <el-form-item label=":" prop="endDate">
              <el-date-picker class="date-start" type="date" v-model="form.endDate" clearable></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode === 0 && form.type === 1">
            <el-form-item label=":" prop="conditionStateDate">
              <el-date-picker v-model="form.conditionStateDate" type="date" placeholder=""></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode != 0 && form.mode == 2">
            <el-form-item label=":" prop="week">
              <el-select multiple collapse-tags v-model="form.week" placeholder="" filterable>
                <el-option
                  v-for="(item,index)  in weekOpts"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode != 0 && form.mode == 3">
            <el-form-item label=":" prop="month">
              <el-select multiple collapse-tags v-model="form.month" placeholder="" filterable>
                <el-option
                  v-for="(item,index)  in monthOpts"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode != 0 && form.mode == 3">
            <el-form-item label=":" prop="day">
              <el-select multiple collapse-tags v-model="form.day" placeholder="" filterable>
                <el-option
                  v-for="(item,index)  in dayOpts"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode != 0 && form.mode != 3">
            <el-form-item label=":" prop="interval">
              <el-input v-model.number="form.interval" :min="0" :max="999"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode == 0 && form.type != 1">
            <el-form-item label=":" prop="planRunStartDate">
              <el-date-picker
                type="datetime"
                clearable
                format="yyyy-MM-dd HH:mm"
                v-model="form.planRunStartDate"
                placeholder=""
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.mode != 0 && form.type != 1">
            <el-form-item label=":" prop="planRunStartTime">
              <el-time-picker format="HH:mm" v-model="form.planRunStartTime"></el-time-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.type == 1">
            <el-form-item label=":" prop="condition">
              <el-time-picker
                is-range
                v-model="form.condition"
                format="HH:mm"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                placeholder=""
              ></el-time-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.type == 1">
            <el-form-item label=":" @change="clearValidate" prop="conditionType">
              <el-select v-model="form.conditionType" placeholder="" filterable>
                <el-option
                  v-for="(item,index)  in conditionTypeOpts"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="form.type == 1">
            <el-form-item label=":" prop="conditionValue">
              <el-input :controls="false" v-model.number="form.conditionValue" :min="0" :max="5000"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="strategy_actionSet">
      <ActionSet :clsRoomID="roomID" ref="ActionSet" />
    </div>
    <p>
      <el-button-group>
        <el-button
          class="add"
          type="primary"
          icon="el-icon-circle-plus-outline"
          size="mini"
          v-if="isAdd"
          @click="savePlan"
        ></el-button>
        <el-button
          class="update"
          type="primary"
          icon="el-icon-edit"
          size="mini"
          @click="savePlan"
          v-if="!isAdd"
        ></el-button>
        <el-button
          plain
          type="warning"
          icon="el-icon-edit"
          size="mini"
          v-if="!isAdd && fixState == 1"
          @click="modifyState(0)"
        ></el-button>
        <el-button
          plain
          type="success"
          icon="el-icon-edit"
          size="mini"
          v-if="!isAdd && fixState == 0"
          @click="modifyState(1)"
        ></el-button>
        <el-button
          class="delete"
          type="danger"
          icon="el-icon-close"
          size="mini"
          @click="handleDelete"
          v-if="!isAdd"
        ></el-button>
      </el-button-group>
    </p>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { FormatDate } from "@/scripts/utils";
export default {
  components: {
    ActionSet: () => import("@/views/ctrl/components/ActionSet")
  },
  props: {
    // ID
    clsRoomID: {
      type: String,
      default: ""
    }
  },
  watch: {
    clsRoomID(newVal, oldVal) {
      if (newVal) {
        setTimeout(() => {
          this.roomID = newVal;
          this.init();
        }, 200);
      }
    }
  },
  data() {
    var validateSDate = (rule, value, callback) => {
      if (value === null) {
        callback(new Error(""));
      } else {
        if (this.form.endDate !== "") {
          this.$refs.form.validateField("endDate");
        }
        callback();
      }
    };
    var validateEDate = (rule, value, callback) => {
      if (value === null) {
        callback(new Error(""));
      } else if (value < this.form.startDate) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      stras: [],
      isAdd: true,
      selIndex: -1,
      form: {
        name: "",
        mode: 0,
        type: 0,
        startDate: "",
        endDate: "",
        week: [],
        month: [],
        day: [],
        interval: 0,
        planRunStartDate: "",
        planRunStartTime: "",
        conditionStateDate: "",
        condition: [new Date(2020, 1, 1, 8, 0), new Date(2020, 1, 1, 18, 0)],
        conditionType: 0,
        conditionValue: 0
      },
      rules: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        mode: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        type: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        startDate: [
          {
            required: true,
            validator: validateSDate,
            trigger: "blur"
          }
        ],
        endDate: [
          {
            required: true,
            validator: validateEDate,
            trigger: "blur"
          }
        ],
        week: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        month: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        day: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        interval: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { type: "number", message: "" }
        ],
        planRunStartDate: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        planRunStartTime: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        condition: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        conditionStateDate: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        conditionType: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        conditionValue: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { type: "number", message: "" }
        ]
      },
      modeOpts: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        },
        {
          value: 3,
          label: ""
        }
      ],
      typeOpts: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        }
      ],
      weekOpts: [
        { value: 1, label: "" },
        { value: 2, label: "" },
        { value: 3, label: "" },
        { value: 4, label: "" },
        { value: 5, label: "" },
        { value: 6, label: "" },
        { value: 0, label: "" }
      ],
      monthOpts: [
        { value: 1, label: "" },
        { value: 2, label: "" },
        { value: 3, label: "" },
        { value: 4, label: "" },
        { value: 5, label: "" },
        { value: 6, label: "" },
        { value: 7, label: "" },
        { value: 8, label: "" },
        { value: 9, label: "" },
        { value: 10, label: "" },
        { value: 11, label: "" },
        { value: 12, label: "" }
      ],
      dayOpts: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
        { value: 3, label: "3" },
        { value: 4, label: "4" },
        { value: 5, label: "5" },
        { value: 6, label: "6" },
        { value: 7, label: "7" },
        { value: 8, label: "8" },
        { value: 9, label: "9" },
        { value: 10, label: "10" },
        { value: 11, label: "11" },
        { value: 12, label: "12" },
        { value: 13, label: "13" },
        { value: 14, label: "14" },
        { value: 15, label: "15" },
        { value: 16, label: "16" },
        { value: 17, label: "17" },
        { value: 18, label: "18" },
        { value: 19, label: "19" },
        { value: 20, label: "20" },
        { value: 21, label: "21" },
        { value: 22, label: "22" },
        { value: 23, label: "23" },
        { value: 24, label: "24" },
        { value: 25, label: "25" },
        { value: 26, label: "26" },
        { value: 27, label: "27" },
        { value: 28, label: "28" },
        { value: 29, label: "29" },
        { value: 30, label: "30" },
        { value: 31, label: "31" },
        { value: 0, label: "" }
      ],
      conditionTypeOpts: [
        { value: 0, label: "" },
        { value: 1, label: "" },
        { value: 2, label: "" },
        { value: 3, label: "" }
      ],
      roomID: "", // ID
      fixID: "", // ID
      fixState: 0
    };
  },
  mounted() {},
  methods: {
    // 
    async init() {
      this.selIndex = -1;
      this.isAdd = true;
      this.stras = [];
      this.emptyForm();
      const res = await api.IOT.GetIoTPlanData(this.roomID);
      if (res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(ele => {
          this.stras.push({
            id: ele.PlanID,
            name: ele.PlanName,
            state: ele.PlanState
          });
        });
      }
    },
    // 
    async selection(obj, index) {
      this.selIndex = index;
      this.isAdd = false;
      this.emptyForm();
      const res = await api.IOT.GetIoTPlanByID(obj.id);
      if (res.type) {
        this.$message({
          message: res.message,
          type: res.type
        });
      } else {
        if (res.Result) {
          if (res.Datas !== null && res.Datas.length > 0) {
            const Key = res.Datas[0].Key;
            this.fixID = Key.PlanID;
            this.fixState = Key.PlanState;
            this.form.name = Key.PlanName;
            this.form.mode = Key.RepeatMode;
            this.form.type = Key.PlanType;

            this.form.interval = Key.PlanInterval;
            if (Key.PlanStart && Key.RepeatMode !== 0) {
              this.form.startDate = FormatDate(Key.PlanStart, "yyyy-MM-dd");
            }
            if (Key.PlanStart && Key.RepeatMode === 0 && Key.PlanType === 1) {
              this.form.conditionStateDate = FormatDate(
                Key.PlanStart,
                "yyyy-MM-dd"
              );
            }
            if (Key.PlanEnd) {
              this.form.endDate = FormatDate(Key.PlanEnd, "yyyy-MM-dd");
            }
            if (Key.RepeatMode === 0) {
              this.form.planRunStartDate = FormatDate(
                Key.PlanRunStart,
                "yyyy-MM-dd hh:mm"
              );
            } else {
              this.form.planRunStartTime = FormatDate(
                Key.PlanRunStart,
                "yyyy-MM-dd hh:mm"
              );
            }
            if (Key.PlanWeek) {
              const week = JSON.parse(Key.PlanWeek);
              week.forEach(w => {
                this.form.week.push(parseInt(w.Value));
              });
            }
            if (Key.PlanMonth) {
              const month = JSON.parse(Key.PlanMonth);
              month.forEach(m => {
                this.form.month.push(parseInt(m.Value));
              });
            }
            if (Key.PlanDay) {
              const day = JSON.parse(Key.PlanDay);
              day.forEach(d => {
                this.form.day.push(parseInt(d.Value));
              });
            }
            if (Key.ConditionStart && Key.ConditionEnd) {
              this.form.condition[0] = FormatDate(
                Key.ConditionStart,
                "yyyy-MM-dd hh:mm"
              );
              this.form.condition[1] = FormatDate(
                Key.ConditionEnd,
                "yyyy-MM-dd hh:mm"
              );
            }
            if (Key.PlanCondition) {
              const cond = JSON.parse(Key.PlanCondition);
              this.form.conditionType = cond.type;
              this.form.conditionValue = cond.value;
            }
            this.$refs.ActionSet.actions = [];
            res.Datas.forEach(element => {
              const actionModel = {
                actionType: "", //  0- 1-
                type: "", // 
                devName: "", // 
                serialID: "", // 
                action: "", // 
                eq: "", // 
                eqName: "", // 
                w: "", // 
                y: "", // 
                b0: "", // 
                b6: "", // 
                panelName: "", // 
                panelType: "", //  1- 0
                actionName: "", // 
                index: "", // 
                code: "", // /
                scene: "", // 
                sceneName: "", // 
                time: "" // 
              };
              if (element.ActionType === "0") {
                const content = JSON.parse(element.ActionContent);
                actionModel.actionType = parseInt(content.actionType);
                actionModel.type = parseInt(content.type);
                actionModel.devName = content.devName;
                actionModel.serialID = content.serialID;
                actionModel.action = content.action
                  ? parseInt(content.action)
                  : "";
                actionModel.eq = content.eq ? parseInt(content.eq) : "";
                actionModel.eqName = content.eqName;
                actionModel.w = content.w ? parseInt(content.w) : "";
                actionModel.y = content.y ? parseInt(content.y) : "";
                actionModel.b0 = content.b0 ? parseInt(content.b0) : "";
                actionModel.b6 = content.b6 ? parseInt(content.b6) : "";
                actionModel.panelName = content.panelName;
                actionModel.panelType = content.panelType
                  ? parseInt(content.panelType)
                  : "";
                actionModel.actionName = content.actionName;
                actionModel.index = content.index
                  ? parseInt(content.index)
                  : "";
                actionModel.code = content.code;
                actionModel.time = content.time ? parseInt(content.time) : "";
              } else {
                actionModel.type = 0;
                actionModel.actionType = element.ActionType;
                actionModel.scene = element.SenceType;
                actionModel.sceneName = "";
                this.$parent.dataModel[7].scene[0].forEach(s => {
                  if (s.type === element.SenceType) {
                    actionModel.sceneName = s.name;
                  }
                });
              }
              this.$refs.ActionSet.actions.push(actionModel);
            });
          }
        }
      }
    },
    // 
    increase() {
      this.selIndex = -1;
      this.isAdd = true;
      this.emptyForm();
    },
    // 
    clearValidate() {
      this.$refs["form"].clearValidate();
    },
    // 
    emptyForm() {
      this.fixID = "";
      this.fixState = 0;
      this.form.name = "";
      this.form.mode = 0;
      this.form.type = 0;
      this.form.startDate = "";
      this.form.endDate = "";
      this.form.week = [];
      this.form.month = [];
      this.form.day = [];
      this.form.interval = 0;
      this.form.planRunStartDate = "";
      this.form.planRunStartTime = "";
      this.form.conditionStateDate = "";
      this.form.condition = [
        new Date(2020, 1, 1, 8, 0),
        new Date(2020, 1, 1, 18, 0)
      ];
      this.form.conditionType = 0;
      this.form.conditionValue = 0;
      this.$refs.ActionSet.actions = [];
    },
    // 
    async savePlan() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          let start = "";
          let end = "";
          let month = "";
          let week = "";
          let day = "";
          const mode = this.form.mode;
          let interval = 0;
          const type = this.form.type;
          let runStart = "";
          let condStart = "";
          let condEnd = "";
          let condType = 0;
          let condValue = 0;
          const desc = "";
          const actions = this.$refs.ActionSet.actions;
          if (type === 0) {
            switch (mode) {
              case 0:
                start = this.form.planRunStartDate;
                runStart = this.form.planRunStartDate;
                break;
              case 1:
                start = this.form.startDate;
                end = this.form.endDate;
                interval = this.form.interval;
                runStart = this.form.planRunStartTime;
                break;
              case 2:
                this.form.week.forEach(element => {
                  week += element + ",";
                });
                start = this.form.startDate;
                end = this.form.endDate;
                interval = this.form.interval;
                runStart = this.form.planRunStartTime;
                break;
              case 3:
                this.form.month.forEach(m => {
                  month += m + ",";
                });
                this.form.day.forEach(d => {
                  day += d + ",";
                });
                start = this.form.startDate;
                end = this.form.endDate;
                runStart = this.form.planRunStartTime;
                break;
            }
          } else {
            condStart = this.form.condition[0];
            condEnd = this.form.condition[1];
            condType = this.form.conditionType;
            condValue = this.form.conditionValue;
            switch (mode) {
              case 0:
                start = this.form.conditionStateDate;
                break;
              case 1:
                start = this.form.startDate;
                end = this.form.endDate;
                interval = this.form.interval;
                break;
              case 2:
                this.form.week.forEach(element => {
                  week += element + ",";
                });
                start = this.form.startDate;
                end = this.form.endDate;
                interval = this.form.interval;
                break;
              case 3:
                this.form.month.forEach(m => {
                  month += m + ",";
                });
                this.form.day.forEach(d => {
                  day += d + ",";
                });
                start = this.form.startDate;
                end = this.form.endDate;
                break;
            }
          }
          if (actions !== null && actions.length > 0) {
            const res = await api.IOT.SaveIoTPlan(
              this.fixID,
              this.clsRoomID,
              this.form.name,
              start,
              end,
              month,
              week,
              day,
              mode,
              interval,
              type,
              runStart,
              condStart,
              condEnd,
              condType,
              condValue,
              desc,
              JSON.stringify(actions)
            );
            if (res.Result) {
              this.init();
              this.$emit("getStrategy", this.roomID);
            }
          } else {
            this.$message({
              showClose: true,
              message: "",
              type: "warning"
            });
          }
        }
      });
    },
    // 
    async modifyState(state) {
      const res = await api.IOT.UpdateIoTPlanEstate(this.fixID, state);
      if (res.type) {
        this.$message({
          message: res.message,
          type: res.type
        });
      }
      this.init();
      this.$emit("getStrategy", this.roomID);
    },
    // 
    async handleDelete() {
      const res = await api.IOT.DeleteIoTPlan(this.fixID);
      if (res.type) {
        this.$message({
          message: res.message,
          type: res.type
        });
      }
      this.init();
      this.$emit("getStrategy", this.roomID);
    }
  }
};
</script>

<style lang="less">
.stratergy {
  height: 100%;
  width: 100%;
  .strategyList {
    width: 90%;
    height: 20%;
    padding: 0px 20px;
    display: grid;
    grid-gap: 8px;
    grid-template-columns: repeat(4, auto);
    -webkit-box-pack: justify;
    justify-content: space-between;
    overflow: hidden;
    overflow-y: auto;
    > div {
      cursor: pointer;
      height: 80px;
      width: 110px;
      border: 1px solid #e8e8e8;
      display: grid;
      grid-template-columns: 100%;
      justify-items: center;
      -ms-flex-line-pack: center;
      align-content: center;
