<template>
  <div class="item course-ware">
    <div class="item-hd">
      <div class="line-title"></div>
      <div>
        <el-button type="primary" size="mini" :disabled="disabled" @click="searchData"></el-button>
        <el-button type="primary" size="mini" :disabled="disabled" @click="beforeShowAdd(null)"></el-button>
      </div>
    </div>

    <div id="table" v-loading="searchLoading">
      <!---->
      <Table ref="inCourseTable" v-if="attribute === 2" :ShowTopBar="false" :ShowTblFoot="false"
             :ShowSelectColumn="false"
             :ShowExport="false" :Columns="dataColumns" :Datas.sync="dataList" :UnNeedEstateBtn="true"
             @LoadData="searchData" :showMaxeight="false">
        <template slot="name" slot-scope="scope">
          <span>{{scope.row.name}}</span>
        </template>
        <template slot="size" slot-scope="scope">
          <span v-if="scope.row.size>1024*1024">{{Math.round( scope.row.size/1024.0/10.24)/100}}MB</span>
          <span v-else>{{Math.round(scope.row.size/10.24)/100}}KB</span>
        </template>
        <template slot="createTime" slot-scope="scope">
          <span>{{formatDate(scope.row.createTime, 'yyyy-MM-dd hh:mm:ss')}}</span>
        </template>
        <template slot="operate" slot-scope="scope">
          <el-button type="danger" icon="el-icon-close" size="mini" plain onfocus="this.blur()"
                     @click="dataDrop(scope.row)">
            
          </el-button>
        </template>
      </Table>

      <!---->
      <el-table v-else :data="classDataList" :default-expand-all="false" max-height="550" style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <Table ref="outCourseTable" :ShowTopBar="false" :ShowTblFoot="false" :ShowExport="false"
                   :ShowSelectColumn="false"
                   :Columns="dataColumns" :Datas.sync="props.row.dataList" :UnNeedEstateBtn="true"
                   @LoadData="searchData" :showMaxeight="false">
              <template slot="size" slot-scope="scope">
                <span v-if="scope.row.size>1024*1024">
                  {{Math.round( scope.row.size/1024.0/10.24)/100}}MB
                </span>
                <span v-else>
                  {{Math.round(scope.row.size/10.24)/100}}KB
                </span>
              </template>
              <template slot="createTime" slot-scope="scope">
                <span>{{formatDate(scope.row.createTime, 'yyyy-MM-dd hh:mm:ss')}}</span>
              </template>
              <template slot="operate" slot-scope="scope">
                <el-button-group>
                  <el-button plain type="danger" icon="el-icon-close" size="mini" @click="dataDrop(scope.row)">
                    
                  </el-button>
                  <el-button plain type="primary" icon="el-icon-time" size="mini" @click="studentTask(scope.row)">
                    
                  </el-button>
                </el-button-group>
              </template>
            </Table>
          </template>
        </el-table-column>
        <el-table-column label="" prop="className"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button plain type="primary" size="mini" icon="el-icon-edit" @click="beforeShowAdd(scope.row)">
              
            </el-button>
            <!--            <el-button plain type="danger" size="mini" @click="dataDrop(scope.row)"></el-button>-->
            <!--            <el-button plain type="primary" size="mini" @click="studentTask(scope.row)"></el-button>-->
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog title="" :visible.sync="showEdit" :close-on-click-modal="false" :modal-append-to-body="false"
               width="800px" center class="dialog" @close="closeDialog">
      <div v-if="attribute !== 2" class="head_select">
        
        <el-select v-model="classID" placeholder="" style="width:450px;"
                   :disabled="disabled || preSelectFile.length>0" @change="classChange">
          <el-option v-for="(item, index) in classOptions" :key="index" :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="head_btn">
        <el-upload ref="upload" action :limit="1" :before-upload="beforeUpload">
          <el-button size="small" icon="el-icon-upload" :disabled="unOperate" type="primary"></el-button>
        </el-upload>
        <div style="font-size:12px;">500MB</div>
        <div style="text-align: right;">
          
          <el-progress :text-inside="true" :stroke-width="13" :percentage="progress" status="success"
                       style="width: 120px;float: right;line-height: 20px;">
          </el-progress>
        </div>
      </div>

      <div v-loading="dataLoading">
        <el-transfer v-model="selectFiles" :data="fileDataList" filterable :props="propsOption" v-show="!unOperate"
                     :titles="['', '']" @change="selectFileChange">
        </el-transfer>
      </div>

      <div class="foot_btn">
        <el-button size="small" @click="showEdit=false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import { FormatDate, getSession, setSession } from "@/scripts/utils";

export default {
  components: {
    Table: () => import("@/components/Table")
  },
  props: {
    attribute: {
      type: Number,
      default: 1
    },
    classOptions: {
      type: Array,
      default: () => {
        return [];
      }
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      classHourID: getSession("campus_ClassHourID"),
      showEdit: false,
      searchLoading: false,
      dataColumns: [
        {
          Code: "name",
          Header: "",
          Fixed: "left"
        },
        {
          Code: "fileType",
          Header: ""
        },
        {
          Code: "size",
          Header: "",
          Slot: true
        },
        {
          Code: "createTime",
          Header: "",
          Slot: true
        },
        {
          Code: "operate",
          Header: "",
          Slot: true,
          width: "450"
        }
      ],
      dataList: [],
      fileTypeOptions: [],
      fileType: "",
      selectFiles: [],
      // 
      preSelectFile: [],
      propsOption: {
        key: "materialId",
        label: "name"
      },
      progress: 0,
      classID: "",
      fileDataList: [],
      dataLoading: false,
      // 
      dialogChange: false,

      classDataList: []
    };
  },
  computed: {
    unOperate () {
      return this.attribute !== 2 && !this.classID;
    }
  },
  created () {
    this.searchData();
  },
  methods: {
    closeDialog () {
      // if (this.dialogChange) this.searchData();
    },
    searchData () {
      this.searchLoading = true;
      this.dataList = [];
      this.selectFiles = [];
      this.preSelectFile = [];
      this.classDataList = [];

      api.ICourseWare.TeacherDataList(this.classHourID, this.attribute).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            const sub = {
              id: item.Key["CID"],
              materialId: item.Key["MaterialID"],
              createTime: item.Key["CreateTime"],
              state: item.Key["State"],
              name: item["MaterialName"],
              size: item["MaterialFileSize"],
              fileType: item["MaterialFileExt"],
              classID: item["ClassID"],
              className: item["ClassName"]
            };
            if (this.attribute === 2) {
              this.dataList.push(sub);
              this.selectFiles.push(item.Key["MaterialID"]);
              this.preSelectFile.push(item.Key["MaterialID"]);
            } else {
              const classId = item["ClassID"] + "";
              const k = this.classDataList.findIndex(
                r => r.classId === classId
              );
              if (k > -1) {
                this.classDataList[k].dataList.push(sub);
              } else {
                this.classDataList.push({
                  classId: classId,
                  className: item["ClassName"],
                  dataList: [sub]
                });
              }
              if (this.classID === classId) {
                this.selectFiles.push(item.Key["MaterialID"]);
                this.preSelectFile.push(item.Key["MaterialID"]);
              }
            }
          });
        }
        if (this.attribute === 2) {
          this.$refs["inCourseTable"].MaxTblHeight = 500;
        } else {
          this.$refs["outCourseTable"].MaxTblHeight = 500;
        }
        this.searchLoading = false;
      }).catch(err => {
        if (err) {
          this.searchLoading = false;
        }
      });
    },
    dataAdd (listId) {
      if (this.attribute !== 2 && this.classID.length === 0) {
        this.$message.error("");
        return false;
      }
      this.dataLoading = true;
      api.ICourseWare.DataSave(
        this.classHourID,
        listId.join(","),
        this.attribute,
        this.attribute !== 2 ? this.classID : this.classOptions.map(r => r.value).join(",")
      ).then(res => {
        if (!res.Result) {
          if (res.Info) this.$message.error(res.Info);
          this.selectFiles = this.preSelectFile;
        } else {
          this.searchData();
        }
        this.dataLoading = false;
      }).catch(() => {
        this.$message.error("");
        this.selectFiles = this.preSelectFile;
        this.dataLoading = false;
      });
    },
    dataDrop (row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.classID = row.classID;
        this.dataDelete([row.id], true);
      });
    },
    dataDelete (listId, showSuccess = false) {
      this.dataLoading = true;
      api.ICourseWare.DataDelete(
        listId.join(","),
        this.attribute !== 2 ? this.classID : this.classOptions.map(r => r.value).join(",")
      ).then(res => {
        if (!res.Result) {
          if (res.Info) this.$message.error(res.Info);
          this.selectFiles = this.preSelectFile;
        } else {
          if (showSuccess) {
            this.$message.success("");
          }
          this.searchData();
        }
        this.dataLoading = false;
      }).catch(() => {
        this.$message.error("");
        this.selectFiles = this.preSelectFile;
        this.dataLoading = false;
      });
    },
    selectFileChange (key) {
      const listAdd = this.selectFiles.filter(r => this.preSelectFile.indexOf(r) === -1);
      const listDelete = this.preSelectFile.filter(r => this.selectFiles.indexOf(r) === -1);

      // 
      if (listAdd.length > 0) {
        this.dataAdd(listAdd);
      } else if (listDelete.length > 0) {
        let ids = [];
        if (this.attribute === 2) {
          ids = this.dataList.map(r => {
            if (listDelete.indexOf(r.materialId) > -1) return r.id;
          });
        } else {
          ids = this.classDataList.find(r => r.classId === this.classID).dataList.map(r => {
            if (listDelete.indexOf(r.materialId) > -1) return r.id;
          });
        }
        this.dataDelete(ids);
      }
      this.dialogChange = true;
    },
    beforeShowAdd (row) {
      if (row) {
        this.classID = row.classId;
        row.dataList.forEach(r => {
          this.selectFiles.push(r.materialId);
          this.preSelectFile.push(r.materialId);
        });
      } else if (this.attribute !== 2) {
        this.classID = "";
        this.selectFiles = [];
        this.preSelectFile = [];
      }
      this.materialGet();
      this.showEdit = true;
    },
    /**
     * 
     */
    materialGet () {
      this.fileDataList = [];
      api.ICourseWare.MaterialDataList().then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.fileDataList.push({
              name: item["MaterialName"],
              materialId: item["MaterialID"],
              size: item["MaterialFileSize"],
              fileType: item["MaterialFileExt"]
            });
          });
        }
      });
    },
    beforeUpload (file) {
      if (this.attribute !== 2 && this.classID.length === 0) {
        this.$message.error("");
        return false;
      }
      if (file.size > 1024 * 1024 * 500) {
        this.$message.error("500MB");
        return false;
      }
      if (file.type.indexOf("cmd") > -1 || file.type.indexOf("exe") > -1 || file.type.indexOf("bat") > -1 || file.type.indexOf("vbs") > -1) {
        this.$message.error("");
        return false;
      }
      // formdata
      const fd = new FormData();
      fd.append("stream", file);
      fd.append("dirid", "");
      fd.append("Token", store.GetToken());
      fd.append("CHID", this.classHourID);
      fd.append("username", store.GetUserInfo().UserName);
      fd.append("attribute", this.attribute);
      fd.append("classID", this.classID);
      const that = this;
      that.progress = 0;
      const onUploadProgress = (progressEvent) => {
        that.progress =
          ((progressEvent.loaded * 100.0) / progressEvent.total) | 0;
      };
      api.ICourseWare.DataUpload(fd, onUploadProgress).then(res => {
        if (!res.Result) {
          if (res.Info) this.$message.error(res.Info);
        } else {
          this.$message({
            showClose: true,
            message: "",
            type: "success"
          });
          const material = {
            name: file.name,
            materialId: res.MaterialID,
            size: file.Size,
            fileType: file.type
          };
          this.dialogChange = true;
          this.fileDataList.push(material);
          // if (this.attribute !== 2) {
          //   this.dataAdd([res.MaterialID]);
          // } else {
          this.searchData();
          // }
        }
      })
        .catch(err => {
          if (err) {
            this.$message({
              showClose: true,
              message: "",
              type: "error"
            });
          }
        });
      return false;
    },

    studentTask (row) {
      setSession("campus_CourseWare", row);
      setSession("campus_TaskType", "CourseWare");
      this.$router.push("/p/task");
    },
    classChange (val) {
      this.selectFiles = [];
      this.preSelectFile = [];
      const item = this.classDataList.find(r => r.classId === val);
      if (item) {
        item.dataList.forEach(r => {
          this.selectFiles.push(r.materialId);
          this.preSelectFile.push(r.materialId);
        });
      }
    },
    formatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    }
  }
}
;
</script>
<style lang="less">
.course-ware {
  .dialog {
    .head_select {
      float: left;
    }

    .head_btn {
      text-align: right;
      margin-bottom: 20px;
    }

    /deep/ .el-transfer-panel {
      width: 320px;
    }

    /deep/ .el-transfer__buttons {
      .el-button {
        display: block;
      }

      .el-button + .el-button {
        margin-left: 0;
      }
    }

    .foot_btn {
      text-align: right;
      margin-top: 20px;
    }
  }
}
</style>
