<template>
  <el-select
    filterable
    :multiple="multiple"
    v-model="Value"
    :disabled="disableds"
    :placeholder="placeholder"
    @change="ChangeHandle"
    clearable
    collapse-tags
    :size="size"
    reserve-keyword
  >
    <el-option
      v-for="(item, index) in Datas"
      :key="index"
      :label="item.DeptName"
      :value="item.DeptID"
    ></el-option>
  </el-select>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    size: {
      type: String,
      default: "mini"
    },
    /**
     * 
     */
    multiple: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    value: {},
    text: {
      type: String,
      default: ""
    },
    TooltipPlacement: {
      type: String,
      default: "right"
    },
    placeholder: {
      type: String,
      default: ""
    }
  },
  data() {
    return { Value: this.value, Datas: [], disableds: this.disabled };
  },
  mounted() {
    this.LoadData();
  },
  watch: {
    value(val) {
      this.Value = val;
      this.LoadData();
    },
    disabled(val) {
      this.disableds = val;
    }
  },
  methods: {
    ChangeHandle(val) {
      this.$emit("update:value", this.Value);
      if (this.Value && this.multiple) {
        const LZString = require("lz-string");
        const jsonStr = JSON.stringify(val);
        let result = "";
        if (jsonStr) {
          result = LZString.compressToBase64(
            jsonStr.slice(1, jsonStr.length - 1)
          );
        }

        this.$emit("Filter", result);
      }
    },
    async LoadData() {
      this.Datas = [];
      const res = await api.Dept.GetAllDeptDatas();
      if (res.Datas) {
        res.Datas.forEach(ele => {
          let name = ele.Key.DeptName;
          if (ele.CampusesName) {
            name = ele.Key.DeptName + " " + "(" + ele.CampusesName + ")";
          }
          this.Datas.push({
            DeptID: ele.Key.DeptID,
            DeptName: name,
            CampusesID: ele.Key.CampusesID
          });
        });
      }
    }
  }
};
</script>

<style lang="less"></style>
