<template>
  <div class="sub_module">

    <div class="module_header">
      <div class="title"></div>
      <!--      <div>-->
      <!--        <el-button type="primary" size="mini" @click="onSearch" style="padding: 7px 15px;"></el-button>-->
      <!--      </div>-->
    </div>

    <Table ref="table"
           :Loading="isLoading"
           :ShowExport="false"
           :ShowCreate="false"
           :ShowSelectColumn="false"
           :ShowTblFoot="false"
           KeyFileld="RLID"
           :SearchValue.sync="keyWord"
           :Columns="Columns"
           :Datas.sync="Datas"
           :UnNeedEstateBtn="true"
           @LoadData="onSearch"
           :showMaxeight="false">
      <template slot="typeNameHeader">
        <el-select v-model="typeSelectedValue" size="mini" placeholder="" @change="onSearch">
          <el-option v-for="item in typeOptions" :key="item.value" :label="item.title" :value="item.value">
          </el-option>
        </el-select>
      </template>
      <template slot="start" slot-scope="scope">
        <span>{{FormatDate(scope.row.start, 'yyyy-MM-dd hh:mm')}}</span>
      </template>
      <template slot="typeName" slot-scope="scope">
        <el-tag :type="typeTag[scope.row.typeValue]" size="mini" v-text="scope.row.typeName"></el-tag>
      </template>
      <template slot="oprate" slot-scope="scope">
        <div>
          <el-button onfocus="this.blur()" type="primary" icon="el-icon-video-play" size="mini" plain
                     @click="liveSelect(scope.row)">
            
          </el-button>
          <el-button onfocus="this.blur()" type="success" icon="el-icon-download" size="mini" plain
                     @click="AllDownload(scope.row)">
            
          </el-button>
        </div>
      </template>
    </Table>

    <el-dialog
      title="" width="80vw"
      :visible.sync="ShowPlayer"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      :before-close="OnDlgPlayerClose"
      :destroy-on-close="true"
      custom-class="dlbPlayer dialog"
      center>
      <el-container>
        <el-main class="dialog_video">
          <r-video ref="liveVideo" :ShowPlayList="false" :media-src="playVideoUrl||''"
                   @PlayEnded="playEnded"></r-video>
        </el-main>
        <el-aside width="300px" class="video-list">
          <template v-if="videoList && videoList.length>0">
            <div v-for="(item, index) in videoList" :key="index"
                 :class="{'item': true, 'video-checked': selectIndex === item.index}" @click="videoSelect(item, index)">
              <img :src="item.playPoster" :onerror="defaultPost" alt=""/>
              <div class="info">
                <div class="title">{{liveSelected.title}}</div>
                <div class="tips">
                  <div class="tip-line">
                    <span>{{ FormatDate(item["MaterialLastAccessDate"], "yyyy-MM-dd hh:mm")}}</span>
                  </div>
                </div>
              </div>
              <div class="item_down" @click.stop="downLoad(item)">
                <i class="el-icon-download"></i>
              </div>
            </div>
          </template>
          <div v-else>
            
          </div>
        </el-aside>
      </el-container>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { FormatDate, getSession } from "@/scripts/utils";
import { recordLessonType, recordLessonTypeName } from "@/scripts/dictionary"

export default {
  name: "LiveLesson",
  components: {
    Table: () => import("@/components/Table"),
    RVideo: () => import("./../../components/RecordDetail/RVideo")
  },
  props: {
    isStudent: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),

      defaultPost: require('@/assets/img/live_lesson_poster.png'),

      typeOptions: [],
      typeSelectedValue: "",
      typeOptionsShow: false,
      typeTag: ["", "primary", "success", "danger", "warning", "info", "info"],

      Columns: [
        {
          Code: "start",
          Header: "",
          Slot: true,
          MinWidth: "250",
          Align: "center",
        },
        {
          Code: "typeName",
          Header: "",
          // HeaderSlot: true,
          Slot: true,
          MinWidth: "150"
        },
        {
          Code: "remark",
          Header: "",
        },
        {
          Code: "oprate",
          Header: "",
          Slot: true,
          Fixed: "right",
          Align: "center",
          Width: "250",
        },
      ],
      Datas: [],

      ShowPlayer: false,

      // typeValue: "",
      isLoading: false,
      keyWord: "",
      liveList: [],
      liveSelected: null,

      videoList: [],
      selectIndex: -1,
      videoItem: null,
      playVideoUrl: "",
      centerHeight: 0
    };
  },
  computed: {},
  created () {
    this.typeOptions = [];
    this.typeOptions.push({
      title: "",
      value: "-1",
      index: 0
    });
    let index = 0;
    for (const item in recordLessonType) {
      index++;
      this.typeOptions.push({
        title: recordLessonTypeName(recordLessonType[item]),
        value: recordLessonType[item] + "",
        index
      });
    }
    this.typeSelectedValue = "-1";
    this.onSearch();
  },
  mounted () {
  },
  methods: {
    FormatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    async onSearch () {
      this.liveList = [];
      // this.videoItem = null;
      // this.videoList = [];
      this.Datas = [];
      this.isLoading = true;

      this.typeSelectedValue = "-1";
      if (this.isStudent) {
        await api.IRecordLesson.StudentDataList(this.courseRecordID).then(res => {
          if (res.Result) {
            res.Datas.forEach((item, index) => {
              const sub = {
                id: item["RLID"],
                deviceID: item["DeviceID"],
                title: item["RLName"],
                start: item["RLStart"],
                end: item["RLEnd"],
                typeValue: item["RLType"] + "",
                typeName: recordLessonTypeName(item.Key["RLType"] + ""),
                remark: item["RLLabel"],
                state: item["RLEstate"],
                allow: item["AllowSee"]
              };
              this.liveList.push(sub);
            });
            this.Datas = this.liveList.slice();
            this.$refs["table"].MaxTblHeight = window.innerHeight - 120;
            // if (this.liveList.length > 0) this.liveSelect(this.liveList[0], 0);
          } else if (res.Info) {
            this.$message.warning(res.Info);
          } else {
          }
          this.isLoading = false;
        }).catch(() => {
          this.isLoading = false;
        });
      } else {
        await api.IRecordLesson.DataList(this.keyWord, this.courseRecordID).then(res => {
          if (res.Result) {
            res.Datas.forEach((item, index) => {
              const listItem = item.Key;
              const sub = {
                id: listItem["RLID"],
                deviceID: listItem["DeviceID"],
                title: listItem["RLName"],
                start: listItem["RLStart"],
                end: listItem["RLEnd"],
                typeValue: listItem["RLType"] + "",
                typeName: recordLessonTypeName(item.Key["RLType"] + ""),
                remark: listItem["RLLabel"],
                state: listItem["RLEstate"],
                allow: listItem["AllowSee"]
              };
              this.liveList.push(sub);
            });
            this.Datas = this.liveList.slice();
            this.$refs["table"].MaxTblHeight = window.innerHeight - 120;
          } else if (res.Info) {
            this.$message.warning(res.Info);
          }
          // this.liveList.length > 0 ? this.liveSelect(this.liveList[0], 0) : this.liveSelect(null, -1);
          this.isLoading = false;
        }).catch(() => {
          this.isLoading = false;
        });
      }
    },
    liveSelect (item) {
      if (item) {
        this.liveSelected = item;
        this.ShowPlayer = true;
        this.getVideoList();
      } else {
        this.videoList = [];
        this.videoSelect(null, -1);
      }
    },
    async AllDownload (item) {
      if (item) {
        this.liveSelected = item;
        await this.getVideoList();
        if (this.videoList.length > 0) {
          const FileSaver = require("file-saver");
          this.videoList.forEach(sub => {
            FileSaver.saveAs(sub.playPath, sub["MaterialID"] + ".mp4");
          });
        } else {
          this.$message.warning("");
        }
      } else {
        this.$message.warning("");
      }
    },
    async getVideoList () {
      this.videoList = [];
      this.selectIndex = -1;
      this.videoItem = null;
      this.isLoading = true;
      await api.IRecordLesson.GetMaterialDatas(
        this.liveSelected.id || ""
      ).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["MaterialCreateDate"] > b["MaterialCreateDate"] ? 1 : -1;
          });

          res.Datas.forEach((item, index) => {
            const playPath = `http://${this.$root.SrvConfigInfo["LiveHost"]}:${this.$root.SrvConfigInfo["LivePort"]}/vod${item["MaterialStorePath"]}`;
            const playPoster = playPath.replace(".mp4", ".png");
            const sub = {
              index,
              ...item,
              playPath,
              playPoster
            };
            this.videoList.push(sub);
          });
        } else if (res.Info) {
          this.$message.warning(res.Info);
        }
        if (this.videoList.length >= 1) this.videoSelect(this.videoList[0]);
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    videoSelect (item) {
      if (item) {
        this.playVideoUrl = item.playPath;
        this.selectIndex = item.index;
        this.videoItem = this.liveList && this.videoList.length > 0 ? this.videoList[this.selectIndex] : null;
        this.$nextTick(() => {
          this.$refs["liveVideo"].Play();
        });
      } else {
        this.playVideoUrl = "http://";
        this.videoItem = null;
        this.selectIndex = -1;
        if (this.$refs["liveVideo"]) this.$refs["liveVideo"].Pause();
      }
    },
    playEnded () {
      if (this.selectIndex < this.videoList.length - 1) {
        const index = this.selectIndex + 1;
        this.videoSelect(this.videoList[index], index);
      }
    },
    OnDlgPlayerClose () {
      this.videoList = [];
      if (this.$refs["liveVideo"]) {
        this.$refs["liveVideo"].Dispose();
      }
      this.$nextTick(() => {
        this.ShowPlayer = false;
      });
    },
    downLoad (item) {
      // const ConfigInfo = store.GetConfigInfo();
      const FileSaver = require("file-saver");
      FileSaver.saveAs(item.playPath, item["MaterialID"] + ".mp4");
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../style/record";

.sub_module {
  margin-bottom: 15px;
}

/deep/ .el-dialog__body {
  padding: 0 !important;
}

.dialog {
  .dialog_video {
    width: calc(80vw - 300px);
    padding: 0;
    height: 36vw;

    &:after {
      clear: both;
    }
  }

  .video-list {
    background-color: #2c2c2c;
    text-align: center;
    color: #fcfcfc;
    height: 36vw;
    overflow-y: auto;

    .item {
      padding: 15px;
      color: #fcfcfc;
      height: 70px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      & img {
        width: 90px;
        height: 68px;
      }

      .info {
        /*margin: 8px 31px 0 98px;*/
        text-align: left;

        .title {
          font-size: 16px;
        }

        .tips {
          margin-top: 8px;

          .tip-line {
            font-size: 12px;
          }
        }
      }

      .item_down {
        width: 30px;
        padding: 19px 2px;
        cursor: pointer;

        &:hover {
          transform: scale(1.2);
        }

        i {
          font-size: 30px;
          cursor: pointer;
        }
      }
    }

    .video-checked {
      background-color: @ThemeColor;
    }
  }
}
</style>
