<template>
  <div class="record_module">
    <div class="module_header">
      <div class="name"></div>
      <div class="more" @click="moreRanking()">
        
        <i class="el-icon-arrow-right"></i>
      </div>
    </div>

    <el-row :gutter="20" v-loading="listLoading">
      <el-col :span="6" v-for="(item, index) in scorePreview" :key="index">
        <el-card shadow="hover" class="list_card blue_bg">
          <el-row :gutter="20">
            <el-col :span="12" class="p-info">
<!--              <el-avatar shape="square" :src="DefaultHeadURL" class="avatar"></el-avatar>-->
              <img src="" class="avatar" v-real-img="[item['UserID'], item['StudentID']]" alt/>
              <div class="name">{{ item.StudentName}}</div>
            </el-col>
            <el-col :span="12" class="card_info">
              <!--              <div class="card_line">-->
              <div>{{item.Ranking}}</div>
              <div>{{item.TotalScore}}</div>
              <div>{{item.score ? item.score : ''}}</div>
              <div>{{item.evaluateText ? item.evaluateText : ''}}</div>
              <!--              </div>-->
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="" :close-on-click-modal="false" :modal-append-to-body="false"
               :visible.sync="dialogTableVisible" class="dialog_list" width="60%">
      <div style="text-align: right; margin-bottom: 10px;">
        <el-button type="primary" size="mini" @click="multiStudentEvaluate" style="padding: 7px 15px;"></el-button>
      </div>
      <Table ref="table" :ShowTopBar="false" :ShowTblFoot="false" :ShowExport="false" :Columns="tableColumns"
             :Datas.sync="studentList" :Loading="listLoading" :KeyFileld="keyIdName" :selections.sync="selectedIds"
             :showMaxeight="false" :SortField.sync="tableSortName" :SortDir.sync="tableSortOrder">
        <template slot="operate" slot-scope="scope">
          <el-button type="primary" size="mini" @click="studentEvaluate(scope.row)" style="padding: 7px 15px;">
          </el-button>
        </template>
      </Table>
    </el-dialog>

    <el-dialog title="" :close-on-click-modal="false" :modal-append-to-body="false" :visible.sync="dialogVisible"
               style="text-align: left;">
      <el-form ref="formComment" :model="formComment" :rules="rulesComment" label-width="80px">
        <el-form-item label="" prop="score">
          <el-input-number v-model="formComment.score" :min="1" :max="100" label="" controls-position="right"
                           :precision="0" :step="1">
          </el-input-number>
          
        </el-form-item>
        <el-form-item label="" prop="comment">
          <el-input type="textarea" v-model="formComment.comment" :autosize="{ minRows: 3, maxRows: 10}"
                    placeholder="" maxlength="500" :show-word-limit="true">
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="evaluate">
          <el-rate v-model="formComment.evaluate" :colors="colors" :texts="texts" show-text style="margin-top: 10px;">
          </el-rate>
        </el-form-item>

        <div style="text-align:right;">
          <el-button @click="dialogVisible = false" style="padding: 7px 15px;"></el-button>
          <el-button :loading="btnLoading" size="mini" type="primary"
                     @click="selectedIds.length > 0 ? saveFormMulti('formComment') : saveForm('formComment')"
                     style="padding: 7px 15px;">
            {{formComment.id ? '' : '' }}
          </el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { getSession } from "@/scripts/utils";

export default {
  name: "StudentScore",
  components: {
    Table: () => import("@/components/Table")
  },
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),
      studentRanking: [
        {
          GDScore: 0,
          PAScore: 0,
          Ranking: 1,
          SAScore: 0,
          StudentID: "20031014485493794201231014163224",
          StudentName: "",
          StudentPhoto: "",
          TotalScore: 0
        }
      ],
      tableColumns: [
        {
          Code: "StudentName",
          Header: "",
          Fixed: "left",
          Width: 80
        },
        {
          Code: "Ranking",
          Header: "",
          Sort: "custom",
          Width: 76
        },
        {
          Code: "SAScore",
          Header: "()",
          Sort: "custom",
          Width: 120
        },
        {
          Code: "GDScore",
          Header: "()",
          Sort: "custom",
          Width: 120
        },
        {
          Code: "PAScore",
          Header: "()",
          Sort: "custom",
          Width: 120
        },
        {
          Code: "TotalScore",
          Header: "",
          Sort: "custom",
          Width: 80
        },
        {
          prop: "score",
          Header: "",
          Sort: "custom",
          Width: 80
        },
        {
          Code: "evaluateText",
          Header: "",
          Sort: "custom",
          Width: 80
        },
        {
          Code: "comment",
          Header: "",
          Width: 200
        },
        {
          Code: "operate",
          Header: "",
          Align: "center",
          Slot: true,
          Width: 100
        }
      ],
      tableSortName: "Ranking",
      tableSortOrder: 0,
      listLoading: false,
      dialogTableVisible: false,

      studentComments: [],
      dialogVisible: false,
      colors: ["#99A9BF", "#F7BA2A", "#FF9900"],
      texts: ["", "", "", "", ""],
      formComment: {
        id: "",
        courseID: "",
        studentID: "",
        comment: "",
        evaluate: 0,
        score: 100
      },
      rulesComment: {
        comment: {
          required: true,
          min: 1,
          max: 500,
          message: " 1  500 ",
          trigger: "blur"
        },
        evaluate: [
          {
            required: true,
            type: "number",
            message: ""
          }
        ],
        score: [
          {
            required: true,
            message: ""
          },
          {
            type: "number",
            message: ""
          }
        ]
      },
      btnLoading: false,
      selectedIds: [],
      keyIdName: "StudentID"
    };
  },
  computed: {
    studentList () {
      return this.studentRanking.map(item => {
        const index = this.studentComments.findIndex(
          r => r["StudentID"] === item["StudentID"]
        );
        const evaluate = index > -1 ? this.studentComments[index]["Evaluate"] : null;
        return {
          ...item,
          score: index > -1 ? this.studentComments[index]["Score"] : null,
          comment: index > -1 ? this.studentComments[index]["Comment"] : null,
          evaluate: evaluate,
          evaluateText: evaluate ? this.texts[evaluate - 1] : ""
        };
      }).sort((a, b) => {
        const orderVal = this.tableSortOrder === 0 ? 1 : -1;
        return a[this.tableSortName] > b[this.tableSortName] ? orderVal : -orderVal;
      });
    },
    scorePreview () {
      return this.studentRanking.filter((r, k) => k < 4).map(item => {
        const index = this.studentComments.findIndex(r => r["StudentID"] === item["StudentID"]);
        const evaluate = index > -1 ? this.studentComments[index]["Evaluate"] : null;
        return {
          ...item,
          score: index > -1 ? this.studentComments[index]["Score"] : null,
          comment: index > -1 ? this.studentComments[index]["Comment"] : null,
          evaluate: evaluate,
          evaluateText: evaluate ? this.texts[evaluate - 1] : ""
        };
      });
    }
  },
  created () {
    this.GetStudentRanking();
    this.getAllStudentEvaluate();
  },
  methods: {
    // 
    GetStudentRanking () {
      this.studentRanking = [];
      this.listLoading = true;
      api.IClassInfo.StudentRankList(this.courseRecordID, this.courseRecord.classID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.studentRanking.push(item);
          });
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      });
    },
    // 
    moreRanking () {
      this.dialogTableVisible = true;
      setTimeout(() => {
        this.$refs["table"].MaxTblHeight = 500;
      }, 0);
    },

    // 
    getAllStudentEvaluate () {
      this.studentComments = [];
      api.ICourseEval.TeacherDataList(this.courseRecordID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.studentComments.push(item);
          });
        }
      });
    },
    multiStudentEvaluate () {
      if (this.selectedIds.length < 1) {
        this.$message({
          message: "",
          type: "warning"
        });
      } else {
        const selectedCommenteds = this.studentComments
          .filter(r => this.selectedIds.indexOf(r["StudentID"]) > -1)
          .map(r => r["CEID"]);
        if (selectedCommenteds.length > 0 && selectedCommenteds.length !== this.selectedIds.length) {
          this.$message.warning("");
        } else {
          this.formComment = {
            id: selectedCommenteds.length > 0 ? selectedCommenteds.join(",") : "",
            studentID: selectedCommenteds.length > 0 ? "" : this.selectedIds.map(r => r["StudentID"]).join(","),
            ciID: this.courseRecordID,
            comment: "",
            evaluate: 0,
            score: 100
          };
          this.dialogVisible = true;
        }
      }
    },
    studentEvaluate (row) {
      this.formComment = {
        id: "",
        studentID: row["StudentID"],
        ciID: this.courseRecordID,
        comment: "",
        evaluate: 0,
        score: 100
      };
      this.getStudentEvaluate();
      this.dialogVisible = true;
    },
    getStudentEvaluate () {
      api.ICourseEval.TeacherDataInfo(
        this.formComment.ciID,
        this.formComment.studentID
      ).then(res => {
        if (res.Result && res.Data) {
          this.formComment.id = res.Data["CEID"];
          this.formComment.comment = res.Data["Comment"];
          this.formComment.evaluate = res.Data["Evaluate"];
          this.formComment.score = res.Data["Score"];
        } else if (res.Info) {
          this.$message.error(res.Info);
        }
      });
    },
    saveFormMulti (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.btnLoading = true;
          api.ICourseEval.DataBatchSave(
            this.formComment.id,
            this.formComment.ciID,
            this.formComment.studentID,
            this.formComment.score,
            this.formComment.comment,
            this.formComment.evaluate,
            false,
            1
          ).then(res => {
            if (res.Result) {
              this.dialogVisible = false;
              this.$message({
                message: "",
                type: "success"
              });
              this.getAllStudentEvaluate();
              this.selectedIds = [];
            } else if (res.Info) {
              this.$message.error(res.Info);
            }
            this.btnLoading = false;
          })
            .catch(() => {
              this.btnLoading = false;
            });
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
    saveForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.btnLoading = true;
          api.ICourseEval.DataSave(
            this.formComment.id,
            this.formComment.ciID,
            this.formComment.studentID,
            this.formComment.score,
            this.formComment.comment,
            this.formComment.evaluate,
            false,
            1
          ).then(res => {
            if (res.Result) {
              this.dialogVisible = false;
              this.$message({
                message: "",
                type: "success"
              });
              this.getAllStudentEvaluate();
            } else if (res.Info) {
              this.$message.error(res.Info);
            }
            this.btnLoading = false;
          }).catch(() => {
            this.btnLoading = false;
          });
        } else {
          this.$message.error("");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "./../../style/record";

.list_card {
  text-align: center;

  .p-info {
    height: 112px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .avatar {
      width: 60px;
      height: 60px;
    }

    .name {
      line-height: 28px;
    }
  }

  .card_info {
    color: #2c3e50;
    line-height: 2;
    font-size: 14px;
    text-align: left;

    .card_line {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
    }
  }
}

.dialog_list {
  /deep/ .el-table .cell.el-tooltip {
    white-space: inherit;
  }
}
</style>
