<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title"></div>
      <div>
        <el-select v-model="groupTime" size="mini" @change="getDiscussSubjectList" :style="{textAlign: 'center'}">
          <el-option v-for="(item, index) in groupList" :key="index" :label="item.text" :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>

    <el-table :data="subjectList" v-loading="discussSubjectLoading" stripe type="expand"
              :default-sort="{prop: 'subject.createTime', order: 'descending'}">
      <el-table-column type="expand">
        <template slot-scope="props">
          <Table :ShowTopBar="false" :ShowTblFoot="false" :ShowSelectColumn="false" :ShowExport="false"
                 :Columns="tableColumns" :Datas.sync="props.row.groupList" :showMaxeight="false"
                 :style="{minHeight: '50px'}">
            <template slot="opreate" slot-scope="scope">
              <el-button type="primary" size="small" @click="viewDetail(scope.row, props.row.subject)"
                         style="padding: 7px 15px;">
                
              </el-button>
            </template>
          </Table>
        </template>
      </el-table-column>
      <el-table-column property="groupList.length" label=""></el-table-column>
      <el-table-column property="subject.time" label="()">
        <template slot-scope="props">
          <span v-if="props.row.subject.time">{{props.row.subject.time}}</span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column property="subject.content" label="">
        <template slot-scope="scope">
          <span v-if="scope.row.subject.type===1">{{ scope.row.subject.content}}</span>
          <el-popover v-else trigger="click">
            <img :src="scope.row.subject.img" alt/>
            <img class="table_img" slot="reference" :src="scope.row.subject.img" alt/>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column property="subject.createTime" label="">
        <template slot-scope="scope">
          <span>{{FormatDate(scope.row.subject.createTime, 'yyyy-MM-dd hh:mm')}}</span>
        </template>
      </el-table-column>
      <el-table-column property="subject.state" label=""></el-table-column>
    </el-table>

    <el-dialog title="" :visible.sync="dialogVisible" :close-on-click-modal="false"
               :modal-append-to-body="false">
      <el-row>
        <el-col :span="18">
          <!--  -->
          <div class="summary">
            <div>
              :
              <span>{{score}}</span>
            </div>
            <div>
              :
              <span>{{good}}</span>
            </div>
            <div>
              :
              <span>{{dataInfo.time === 0 ? '' : dataInfo.time}}</span>
            </div>
          </div>

          <!--  -->
          <div class="chat" ref="chat">
            <div class="chat_title">
              <span class="chat_title_left">[]</span>
              <span v-if="dataInfo.type === contentTypeDefault ">{{ dataInfo.content }}</span>

              <el-popover v-else trigger="click">
                <img :src="dataInfo.img" alt :style="{maxWidth:'800px',maxHeight:'500px'}"/>
                <img :src="dataInfo.img" slot="reference" :style="{maxWidth:'350px',maxHeight:'150px'}" alt/>
              </el-popover>
            </div>

            <div v-if="dataList && dataList.length>0" class="chat_content">
              <div v-for="(sub, k) in dataList" :key="k"
                   :class=" sub.studentId!== selectStudentId ? 'chat_item' : 'chat_item checked_item'">
                <div class="chat_news">
                  <div class="chat_news_name">
                    {{sub.name}}
                    <span>{{sub.time}}</span>
                    <span class="chat_comment" v-if="sub.score">+{{sub.score||0}}</span>
                  </div>

                  <el-badge type="warning" :value="sub.thumb > 0 ? '' : ''">
                    <div class="chat_news_cont">
                      <span v-if="sub.type === contentTypeDefault">{{sub.postContent }}</span>
                      <el-popover v-else trigger="click">
                        <img :src="sub.postImg" alt :style="{maxWidth:'800px',maxHeight:'500px'}"/>
                        <img
                          :src="sub.postImg"
                          slot="reference"
                          :style="{maxWidth:'350px',maxHeight:'150px'}"
                          alt
                        />
                      </el-popover>
                    </div>
                  </el-badge>
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="list_head"></div>
          <div class="list_person">
            <div v-for="(item, index) in personList" :key="index" class="person_item"
                 :class="selectStudentId === item['StudentID'] ? 'person_selected' : ''"
                 @click="selectStudentId=item['StudentID']">
              <img src="" v-real-img="[item['UserID'], item['StudentID']]" alt/>
              <span>{{item['StudentName']}}</span>
              <div>
                <span>{{item['TotalScore']}}</span>
                <span>{{item['TotalGood']}}</span>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { FormatDate, getSession } from "@/scripts/utils";
import { ContentType, ModulePicture } from "@/scripts/dictionary";

export default {
  name: "GroupDiscussion",
  components: { Table: () => import("@/components/Table") },
  props: {
    groupList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),
      groupTime: null,

      discussSubjectList: [],
      discussSubjectLoading: false,

      subjectList: [],
      tableColumns: [
        { Code: "groupName", Header: "", width: 300 },
        { Code: "good", Header: "" },
        { Code: "score", Header: "" },
        { Code: "opreate", Header: "", Slot: true, width: 240 }
      ],

      dialogVisible: false,
      dataInfo: {},
      dataList: [],
      score: 0,
      good: 0,
      contentTypeDefault: ContentType.Text,

      personList: [],
      personLoading: false,
      selectStudentId: ""
    };
  },
  methods: {
    // 
    getPicture (filename, fileType) {
      return this.$root.PictureURL + "?fileName=" + filename + "&fileType=" + fileType + "&platFormType=1";
    },
    initSelect () {
      this.$nextTick(() => {
        if (this.groupList && this.groupList.length > 0) {
          this.groupTime = this.groupList[0].value;
          this.getDiscussSubjectList();
        }
      });
    },
    // 
    getDiscussSubjectList () {
      this.discussSubjectList = [];
      this.discussSubjectLoading = true;
      this.subjectList = [];
      api.IGroupWall.DataList(
        this.courseRecordID,
        this.groupTime
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.discussSubjectList.push({
              ...item,
              img: this.getPicture(
                item["Key"]["GSContent"],
                ModulePicture.GroupSubject
              )
            });

            const subject = {
              type: item["Key"]["GSForm"],
              time: item["Key"]["GSTime"],
              content: item["Key"]["GSContent"],
              createTime: item["Key"]["CreateTime"],
              img: item["Key"]["GSForm"] === 1 ? "" : this.getPicture(item["Key"]["GSContent"], ModulePicture.GroupSubject),
              state: item["Key"]["State"] === 1 ? "" : ""
            };
            const groupInfo = {
              subjectId: item["Key"]["GSID"],
              groupId: item["Key"]["GroupID"],
              groupName: item["GroupName"],
              good: item["TotalGood"],
              score: item["TotalScore"]
            };
            const index = this.subjectList.findIndex(
              r => JSON.stringify(r.subject) === JSON.stringify(subject)
            );
            if (index > -1) {
              this.subjectList[index].groupList.push(groupInfo);
            } else {
              this.subjectList.push({ subject, groupList: [groupInfo] });
            }
          });
        }
        this.discussSubjectLoading = false;
      }).catch(() => {
        this.discussSubjectLoading = false;
      });
    },

    FormatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },

    viewDetail (row, subject) {
      this.dataInfo = subject;
      this.good = row.good || 0;
      this.score = row.score || 0;
      this.getDiscussionDetail(row);
      this.getGroupMember(row);
      this.dialogVisible = true;
    },
    getDiscussionDetail (row) {
      this.dataList = [];
      api.IGroupWall.DataInfo(
        row.subjectId
      ).then(res => {
        if (res.Result) {
          if (res["GAData"] && res["GAData"].length > 0) {
            res["GAData"].forEach(item => {
              const value = {
                id: item["Key"]["GDID"],
                subjectId: item["Key"]["GSID"],
                studentId: item["StudentID"],
                name: item["StudentName"],
                type: item["Key"]["GDForm"],
                postImg: item["Key"]["GDForm"] === ContentType.Picture ? this.getPicture(item["Key"]["GDContent"], ModulePicture.GroupDiscuss) : "",
                postContent: item["Key"]["GDForm"] === ContentType.Picture ? "" : item["Key"]["GDContent"],
                time: this.FormatDate(item["Key"]["SubmitTime"], "hh:mm:ss"),
                thumb: item["Key"]["GDGood"],
                isThumb: 0,
                score: item["Key"]["GDScore"]
              };
              this.dataList.push(value);
            });
          }
        }
      });
    },
    getGroupMember (row) {
      this.personLoading = true;
      this.personList = [];
      api.IGroup.SubjectMemberRank(
        row.groupId,
        row.subjectId
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.personList.push(item);
          });
          this.selectStudentId = res.Datas[0]["StudentID"];
        }
        this.personLoading = false;
      }).catch(() => {
        this.personLoading = false;
      });
    }
  },
  watch: {
    groupList (newVal, oldVal) {
      if (newVal && newVal.length > 0) this.initSelect();
    }
  }
};
</script>
